
.navigation {
  &__border {
    @media (min-width: $tablet-width) {
      border-bottom: 2px solid $basic-green;
      padding-bottom: 8px;
    }
  }

  &__border--white {
    @media (min-width: $desktop-width) {
      border-bottom: 2px solid $white;
    }
  }

  @media (min-width: $tablet-width) {
    margin-left: auto;
  }
}

