.navigation__list a {
  @include oswald(20px, 20px, 500);
  text-transform: uppercase;

  @media (max-width: $mobile-width-only) {
    display: flex;
    padding: 18px 15px 26px;
    justify-content: center;
  }

  @media (min-width: $tablet-width) {
    @include oswald(18px, 24px);
  }

  @media (min-width: $desktop-width) {
    @include oswald(20px, 30px);
  }
}

.navigation__list a::after {
  @media (max-width: $mobile-width-only) {
    content: "";
    width: 100vw;
    position: absolute;
    border-bottom: 1px solid $basic-gray;
    margin-top: -23px;
  }
}


.navigation__list a:hover {
  opacity: 0.6;
}

.navigation__list a:active {
  opacity: 0.3;
}

.navigation__list--color a {
  @media (min-width: $desktop-width) {
    color: white;
  }
}

