.navigation__list--form-desktop a {
  color: $basic-black;
}

.navigation__list {
  @media (max-width: $mobile-width-only) {
    justify-content: center;
    text-align: center;
    grid-row-gap: 46px;
    margin-top: 10px;
    padding-top: 10px;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    margin-left: 91px;
  }
}

.navigation__list li:not(:first-child) {
  @media (min-width: $tablet-width) {
    margin-left: 35px;
  }
}
